* {
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

html {
  height: 100%;
  font-size: 10px;
}

body {
  height: 100%;
  color: azure;
  background-color: $bgColor;
  background: url('../../img/finish.jpg');
  background-size: 100% ;
  background-repeat:repeat-y;
  background-position: bottom;
  & > div {
  height: 100%;
  }
 
}
.App {
  height: 100%;
  display: flex;
  flex-direction: column;

}
